<script>
  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

  export let array = [];

  let count = 0;

  const iterateImg = iterator => {
    if (iterator > 0) {
      if (count + 1 === array.length) {
        count = 0;
      } else {
        count = count + iterator;
      }
    } else {
      if (count === 0) {
        count = array.length - 1;
      } else {
        count = count + iterator;
      }
    }
  };

  const handleClick = index => {
    dispatch('changeImg', index);
  };
</script>

<style type="text/scss">@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;1,400&display=swap");
.wrapper {
  position: relative;
  width: 100%;
  height: 8rem;
  margin: 0.5rem 0;
  overflow: hidden; }
  .wrapper .next,
  .wrapper .prev {
    position: absolute;
    top: 0;
    height: 100%;
    width: 2rem;
    border: none;
    font-size: 1.25rem;
    background: rgba(225, 186, 127, 0.5);
    color: white;
    z-index: 10; }
    .wrapper .next:hover,
    .wrapper .prev:hover {
      background: rgba(225, 186, 127, 0.9); }
  .wrapper .prev {
    left: 0; }
  .wrapper .next {
    right: 0; }
  .wrapper .slider {
    position: absolute;
    top: 0;
    display: flex;
    flex-flow: row nowrap;
    height: inherit;
    width: auto;
    background: pink;
    transition: left 0.3s ease-in-out; }
    .wrapper .slider .img {
      position: relative;
      height: 8rem;
      width: 12rem;
      min-width: 12rem;
      border: 1px solid white;
      background-size: cover;
      background-color: #6a706e; }
      .wrapper .slider .img:first-of-type {
        border-left: none; }
      .wrapper .slider .img:last-of-type {
        border-right: none; }
</style>

<div class="wrapper">
  <button class="prev" on:click={() => iterateImg(-1)}>&ltrif;</button>
  <div class="slider" style={`left: -${12 * count}rem;`}>
    {#each array as img, i}
      <div
        class="img"
        style={`background-image: url(${img});`}
        on:click={() => handleClick(i)} />
    {/each}
    <div class="div img" />
  </div>
  <button class="next" on:click={() => iterateImg(1)}>&rtrif;</button>
</div>
