<script>
  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

  export let name;
  export let number;
  export let current = false;
  export let selectedDate;
  export let value;

  const updateSelected = () => dispatch('updateSelected', value);
</script>

<style type="text/scss">@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;1,400&display=swap");
.wrapper {
  position: relative;
  display: block;
  width: 38px;
  height: 38px;
  overflow: hidden;
  background: white;
  border-radius: 100%;
  cursor: pointer;
  transition: background 0.2s ease-in-out; }
  .wrapper input {
    position: absolute;
    top: 0;
    right: -40px;
    visibility: hidden; }
  .wrapper .number {
    margin: 0;
    margin-top: -0.1rem;
    font-family: "Playfair Display";
    line-height: 40px;
    text-align: center;
    letter-spacing: 0;
    color: #080708; }
  .wrapper--current {
    background: #e1ba7f; }
    .wrapper--current .number {
      color: white; }
  .wrapper--true {
    background: #1b998b; }
    .wrapper--true .number {
      color: white; }
</style>

<label
  class={`wrapper wrapper--${selectedDate === value ? true : false}`}
  class:wrapper--current={current}>
  <input type="checkbox" {name} {value} on:click={updateSelected} />
  <p class="number">{number}</p>
</label>
